.side-drawer {
  position: fixed;
  left: 0;  
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: #8ba0a4;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);  

  display: flex;
  flex-direction: column;
  padding: 5rem;
}

@media (min-width: 768px) {
  .side-drawer {
    display: none;
  }
}

